@charset "UTF-8";
@import '../core/index-noreset.scss';
@import '../input/scss/variable.scss';
@import '../input/scss/mixin.scss';
@import 'scss/mixin';
@import 'scss/variable';

#{$select-prefix} {
  /* stylelint-disable-next-line block-opening-brace-space-before */
  &-loading {
    // Dialogloading100%
    height: auto !important;
    min-height: 44px; // Loading48px
    .#{$css-prefix}loading-component {
      opacity: 1 !important;
    }
  }
  @include box-sizing;
  display: inline-block;
  font-size: 0;
  vertical-align: middle;

  &-menu &-menu-item {
    padding: 0 $S12 0 $S40 - (14px - $font-size-body)*2;
    .#{$css-prefix}icon {
      font-size: $S20 - (14px - $font-size-body)*2;
      left: $S12;
    }
  }

  /* Select trigger */
  &-trigger {
    min-width: 150px;
    outline: 0;
    transition: border-color 0.3s ease;

    .#{$css-prefix}input-label {
      flex: 0 0 auto;
      width: auto;
    }

    #{$select-prefix}-values {
      display: block;
      width: 100%;
      flex: 1 1 0;
      overflow: hidden;

      > em {
        font-style: inherit;
      }

      input {
        padding-left: 0;
        padding-right: 0;
        height: 100%;
      }
    }

    .#{$css-prefix}input-control {
      flex: 0 0 auto;
      width: auto;
      > * {
        display: inline-block;
        width: auto;
      }
      > .#{$css-prefix}select-arrow {
        padding-right: 0;
        .#{$css-prefix}icon {
          vertical-align: middle;
        }
      }
    }

    .#{$css-prefix}input.#{$css-prefix}disabled {
      em {
        color: $gray-05;
      }
      #{$select-prefix}-arrow {
        cursor: not-allowed;
      }
    }

    .#{$css-prefix}select-clear {
      display: none;
    }

    &.#{$css-prefix}has-clear:hover {
      .#{$css-prefix}select-clear {
        display: inline-block;
      }
      .#{$css-prefix}select-arrow {
        display: none;
      }
    }
  }

  #{$select-prefix}-inner {
    display: inline-flex;
    align-items: center;
    width: 100%;
    min-width: 100px;

    outline: 0;
    color: $select-color;

    #{$tag-prefix} {
      line-height: 1;
      margin-right: $select-tag-spacing-lr;
      margin-bottom: $select-tag-spacing-tb;
      padding-left: $select-tag-padding-lr;
      padding-right: $select-tag-padding-lr;
    }

    .#{$css-prefix}input-inner {
      width: auto;
    }
  }

  &-trigger-search {
    position: relative;
    display: inline-block;
    vertical-align: top;
    overflow: hidden;
    width: 100%;
    max-width: 100%;

    > input,
    > span {
      display: block;
      font-size: inherit;
      font-family: inherit;
      letter-spacing: inherit;
      white-space: nowrap;
      overflow: hidden;
    }

    input {
      position: absolute;
      background-color: transparent;
      width: 100%;
      z-index: 1;
      left: 0;
      border: 0;
      outline: 0;
      margin: 0;
      padding: 0;
      cursor: inherit;
    }

    > span {
      position: relative;
      visibility: hidden;
      white-space: pre;
      max-width: 100%; /*  table 100% x */
      z-index: -1;
    }
  }

  &-single {
    &.#{$css-prefix}no-search {
      cursor: pointer;
    }

    /*  */
    &.#{$css-prefix}has-search.#{$css-prefix}active {
      #{$select-prefix}-values > em {
        display: none;
      }
    }

    /* menu(placeholder) */
    &.#{$css-prefix}no-search,
    &.#{$css-prefix}inactive {
      #{$select-prefix}-values > em + #{$select-prefix}-trigger-search {
        width: 1px;
        opacity: 0;
        filter: alpha(opacity=0); /* for IE 9 */
      }

      #{$select-prefix}-trigger-search input {
        color: transparent;
        height: 100%;
      }
    }

    #{$select-prefix}-values {
      display: inline-flex;
      align-items: center;

      > em {
        vertical-align: middle;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }

  &-multiple {
    #{$select-prefix}-compact {
      position: relative;
      white-space: nowrap;
      #{$select-prefix}-trigger-search {
        width: auto;
      }
      #{$select-prefix}-tag-compact {
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        padding: 0 4px 0 16px;
        color: $input-text-color;
        background: linear-gradient(90deg, rgba(255, 255, 255, 0), $input-bg-color 10px);
      }
    }
  }

  &-multiple,
  &-tag {
    #{$select-prefix}-values {
      /* Tag  3px  margin-bottom Tag  */
      margin-bottom: -$select-tag-spacing-tb;
      height: auto !important;
    }

    #{$select-prefix}-trigger-search {
      margin-bottom: $select-tag-spacing-tb;
    }

    /* 1px */
    #{$tag-prefix} + #{$select-prefix}-trigger-search {
      width: auto;
      min-width: 1px; /*  */
    }
    .#{$css-prefix}input {
      height: auto;
      align-items: start;
    }

    &.#{$css-prefix}xs {
      #{$select-prefix}-values {
        @include select-size($form-element-xs-height, $select-xs-lineheight);
      }
      #{$select-prefix}-values-compact {
        height: $form-element-xs-height !important;
      }
      #{$tag-prefix} {
        border: 0;
        @include select-tag-size($select-xs-lineheight, -1);

        &-body {
          line-height: $select-xs-lineheight;
        }
      }
      .#{$css-prefix}input-label,
      .#{$css-prefix}input-control,
      #{$select-prefix}-tag-compact {
        line-height: $form-element-xs-height - 2;
      }
    }

    &.#{$css-prefix}small {
      #{$select-prefix}-values {
        @include select-size($form-element-small-height, $select-s-lineheight);
      }
      #{$select-prefix}-values-compact {
        height: $form-element-small-height !important;
      }
      #{$tag-prefix} {
        border: 0;
        @include select-tag-size($select-s-lineheight, -1);

        &-body {
          line-height: $select-s-lineheight;
        }
      }
      .#{$css-prefix}input-label,
      .#{$css-prefix}input-control,
      #{$select-prefix}-tag-compact {
        line-height: $form-element-small-height - 2;
      }
    }

    &.#{$css-prefix}medium {
      #{$select-prefix}-values {
        @include select-size($form-element-medium-height, $select-m-lineheight);
      }
      #{$select-prefix}-values-compact {
        height: $form-element-medium-height !important;
      }
      #{$tag-prefix} {
        @include select-tag-size($select-m-lineheight);
      }
      .#{$css-prefix}input-label,
      .#{$css-prefix}input-control,
      #{$select-prefix}-tag-compact {
        line-height: $form-element-medium-height - 2;
      }
    }
  }

  &.#{$css-prefix}no-search {
    /*  input  color  */
    /* ISSUE:  firefox readonly  input  */
    #{$select-prefix}-trigger-search input {
      color: transparent;
      height: 100%;
    }
  }

  &-auto-complete {
    width: 160px;

    .#{$css-prefix}input {
      width: 100%;
      .#{$css-prefix}input-hint-wrap {
        padding-right: 1px;
      }
      /* table-cell  margin  */
      #{$select-prefix}-arrow {
        padding-left: 0;
      }
    }
  }

  &.#{$css-prefix}active {
    #{$select-prefix}-arrow .#{$css-prefix}icon-chevron-down {
      transform: rotate(180deg);
    }
  }

  &-arrow {
    cursor: pointer;
    width: auto !important;
    text-align: center;
    .#{$css-prefix}icon-chevron-down {
      transition: transform 0.3s ease;
    }
    /* transform-origin: center 41.8%; // icon  */
  }

  &-menu-wrapper {
    max-height: 260px;
    overflow: auto;
    border: $popup-local-border-width $popup-local-border-style $popup-local-border-color;
    border-radius: $popup-local-corner;
    box-shadow: $popup-local-shadow;
    #{$select-prefix}-menu {
      max-height: none;
      border: none;
    }
  }

  /*  */
  &-menu {
    max-height: 260px;
    overflow: auto;

    #{$select-prefix}-menu-empty-content {
      padding-left: $S8;
      padding-right: $S8;
      color: $select-hint-color;
    }

    /* autoComplete  menu  */
    &#{$select-prefix}-auto-complete-menu#{$select-prefix}-menu-empty {
      display: none;
    }

    // .#{$css-prefix}menu-item-text .#{$css-prefix}icon {
    //   vertical-align: middle;
    // }
  }

  /*  */
  &-all {
    display: block;
    cursor: pointer;
    padding: 0 $S8;
    margin: 0 $S12 $S8;
    border-bottom: 1px solid $gray-03;

    &:hover {
      color: $hover-brand;
    }

    .#{$css-prefix}menu-icon-selected.#{$css-prefix}icon {
      // remove display: inline-block !important; in next Y
      display: inline-block !important;
      top: initial;
      color: $select-menu-icon-color;
    }
  }

  &-highlight {
    color: $select-highlight-color;
    font-size: $select-highlight-font;
  }

  /* -------------- IE polyfill -------------- */
  /* PC  IE9  2% */
  /*  IE9  flex polyfill */
  /* @see http://gs.statcounter.com/ */
  /* IE10IE11  flex */
  &-in-ie#{$select-prefix}-trigger {
    #{$select-prefix}-values {
      /*  table overflow: hidden  margin  */
      overflow: visible;
    }

    .#{$css-prefix}input-control,
    .#{$css-prefix}input-label {
      width: 1px;
    }
    .#{$css-prefix}input-control > * {
      display: table-cell;
      width: 1%;
    }
    #{$select-prefix}-arrow {
      display: table-cell;
    }
    #{$select-prefix}-clear {
      display: none;
    }

    &#{$select-prefix}-multiple,
    &#{$select-prefix}-tag {
      #{$select-prefix}-inner {
        vertical-align: top;
      }
    }

    #{$select-prefix}-inner {
      display: inline-table;
    }

    &#{$select-prefix}-single {
      #{$select-prefix}-values {
        display: inline-table;
      }
      .#{$css-prefix}input.#{$css-prefix}xs #{$select-prefix}-values {
        line-height: $form-element-xs-height;
      }
      .#{$css-prefix}input.#{$css-prefix}small #{$select-prefix}-values {
        line-height: $form-element-small-height;
      }
      .#{$css-prefix}input.#{$css-prefix}medium #{$select-prefix}-values {
        line-height: $form-element-medium-height;
      }
    }

    #{$select-prefix}-trigger-search {
      > span {
        max-width: 100px;
      }
    }

    &#{$select-prefix}-single {
      &#{$select-prefix}-in-ie-fixwidth #{$select-prefix}-values {
        position: relative;
        > em {
          position: absolute;
          display: inline-block;
          height: 100%;
          line-height: 1;
          vertical-align: middle;
          overflow: hidden;
          left: 4px;
          right: 0;
          top: 30%;
        }
      }
      /* menu(placeholder) */
      &.#{$css-prefix}no-search,
      &.#{$css-prefix}inactive {
        #{$select-prefix}-values > em + #{$select-prefix}-trigger-search {
          filter: alpha(opacity=0); /* for IE 9 */
          font-size: 0;
        }

        #{$select-prefix}-trigger-search input {
          color: inherit;
        }
      }
    }

    &.#{$css-prefix}no-search {
      #{$select-prefix}-trigger-search input {
        color: inherit;
      }
    }
  }
}

@import './rtl.scss';
