
#{$rating-prefix}[dir="rtl"] {
  #{$rating-prefix}-overlay {
    right: 0;
    left: auto;
  }
  #{$rating-prefix}-underlay,
  #{$rating-prefix}-overlay {
    #{$rating-prefix}-icon {
      margin-right: $S4;
      margin-left: 0;
    }
    #{$rating-prefix}-icon:last-child {
      /* icon  margin  */
      margin-left: $S4;
    }
  }
}
